    <transition>
        <router-view></router-view>
    </transition>
    <footer>
      <ul>
        <li @click="navTab('device')">
          <img src="../../assets/me/equipment@3x.png" alt="" v-if="active === 'device'">
          <img src="../../assets/me/equipment2@3x.png" alt="" v-if="active !== 'device'">
          <span :class ="{active: active === 'device'}">设备</span>
        </li>
        <li @click="navTab('me')">
          <img src="../../assets/me/my2@3x.png" alt="" v-if="active === 'me'">
          <img src="../../assets/me/my@3x.png" alt="" v-if="active !== 'me'">
          <span :class ="{active: active === 'me'}">我的</span>
        </li>
      </ul>
    </footer>
  </div>
</template>

<script>
import headTop from '../../components/common/head'
export default{
  data () {
    return {
      active: 'me'
    }
  },
  components: {
    'head-top': headTop
  },
  computed: {
  },
  created () {
  },
  mounted () {
    console.log(this.$route.name)
    if (this.$route.name === 'device') {
      this.active = 'device'
    }
  },
  methods: {
    navTab (type) {
      this.active = type
      switch (type) {
        case 'device':
          this.$router.push({path: '/Home/device'})
          break
        case 'me':
          this.$router.push({path: '/Home/me'})
          break
        default:
          break
      }
    }
  },
  watch: {}
}
</script>

